<template>
  <div class="classify">
    <div>
      <van-nav-bar
        class="bar"
        :title="id.minor"
        :left-text="id.major"
        left-arrow
        @click-left="onClickLeft"
        @click-right="onClickRight"
      >
        <template #right>
          <van-icon :name="show ? 'cross' : 'ellipsis'" size="18" />
        </template>
      </van-nav-bar>
      <van-popup v-model="show" position="top" :style="{ height: '171.2px'}">
        <van-grid :column-num="3">
          <van-grid-item
            v-for="(item,index) in popup"
            :key="index"
            :icon="item.icon"
            :text="item.text"
            @click="popupClick(item)"
          />
        </van-grid>
      </van-popup>
    </div>
    <div v-if="bookData.length == 0">
      <van-empty description="哎呀，暂时没有你要查找的书本哦！" />
    </div>
    <!-- 分类小说 -->
    <van-list
      v-else
      v-model="loadOptions.isLoading"
      :finished="loadOptions.isFinished"
      :finished-text="loadOptions.finishedText"
      @load="onLoad"
      :offset="50"
    >
      <div
        class="classify-item clearfix"
        v-for="(item,index) in bookData"
        :key="index"
        @click="goPage(item)"
      >
        <div class="classify-img fl">
          <img class="auto-img" v-lazy="item.cover" alt />
        </div>
        <div class="classify-detail fl">
          <div class="classify-title one-text">{{item.title}}</div>
          <div class="classify-author one-text">作者：{{item.author}}</div>
          <div class="classify-shortIntro one-text">简介：{{item.shortIntro}}</div>
          <div class="classify-lastChapter one-text">更新至：{{item.lastChapter}}</div>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      id: {},
      bookData: [],
      allBookData: [],
      loadOptions: {
        //是否处于加载状态，加载过程中不触发load事件
        isLoading: true,
        //是否完成加载所有数据
        isFinished: false,
        finishedText: "没有书本可加载了",
      },
      //每次加载20条数据
      loadCount: 6,
      show: false,
      val: {},
      popup: [
        {
          icon: "home-o",
          text: "主页",
          route: "Home",
        },
        {
          icon: "contact",
          text: "账户",
          route: "Login",
        },
        {
          icon: "search",
          text: "搜索",
          route: "Search",
        },
        {
          icon: "bookmark-o",
          text: "书架",
          route: "Bookshelf",
        },
        {
          icon: "bar-chart-o",
          text: "排行榜",
          route: "Ranking",
        },
      ],
      moreId: {},
    };
  },
  created() {},
  beforeRouteEnter(to, from, next) {
    let ids = to.query;
    console.log("跳转路由前获取路由参数");
    //开启加载提示
    Toast.loading({
      message: "加载中...",
      forbidClick: true,
      //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
      duration: 0,
    });
    //跳转路由前获取路由参数
    next((vm) => {
      //接收小说父分类和字分类
      if (Object.keys(ids).length == 3) {
        vm.id = ids;
        vm.getBookData();
        vm.$toast.clear();
      } else if (Object.keys(ids).length == 2) {
        vm.moreId = ids;
        vm.getBookData();
        vm.$toast.clear();
      } else {
        vm.val = ids;
        vm.getBookData();
        vm.$toast.clear();
      }
    });
  },
  methods: {
    onLoad() {
      setTimeout(() => {
        this.bookData.push(...this.allBookData.splice(0, this.loadCount));
        // 数据全部加载完成
        this.loadOptions.isLoading = false;
        if (this.allBookData.length == 0) {
          this.loadOptions.isFinished = true;
        }
      }, 200);
    },
    //获取分类书本
    getBookData() {
      let url = "";
      if (Object.keys(this.id).length != 0) {
        url =
          "http://novel.kele8.cn/category-info?gender=" +
          this.id.sex +
          "&type=hot&major=" +
          this.id.major +
          "&minor=" +
          this.id.minor +
          "&start=0&limit=1000";
      } else if (Object.keys(this.moreId).length != 0) {
        url = "http://novel.kele8.cn/recommend/" + this.moreId.id;
        this.id.minor = "你可能感兴趣的";
        this.id.major = "返回";
      } else {
        url = "http://novel.kele8.cn/search?keyword=" + this.val.val;
        this.id.minor = "搜索结果";
        this.id.major = "返回";
      }
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0,
      });
      this.axios({
        method: "GET",
        url: url,
      })
        .then((result) => {
          this.$toast.clear();

          if (Object.keys(result.data).length > 0) {
            //对数据加锁图片解码
            for (let i = 0; i < result.data.books.length; i++) {
              result.data.books[i].cover = this.imgKey(
                result.data.books[i].cover
              );
            }
            //赋值
            this.allBookData = result.data.books;
            this.bookData = this.allBookData.splice(0, this.loadCount);

            if (this.allBookData.length > 0) {
              this.loadOptions.isLoading = false;
            } else {
              this.loadOptions.isLoading = true;
              this.loadOptions.isFinished = true;
            }
            if (this.bookData.length == 0) {
              this.loadOptions.finishedText = "";
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //图片解码
    imgKey(value) {
      return unescape(value).slice(7);
    },
    //返回
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      this.show = !this.show;
    },
    //跳转到小说详情页
    goPage(item) {
      let _id = item._id;
      this.$router.push({
        name: "Detail",
        query: { id: _id },
      });
    },
    popupClick(item) {
      this.$router.push({ name: item.route });
    },
  },
};
</script>

