.classify {
  .classify-item {
    box-sizing: border-box;
    border-bottom: 1px solid #ccc;
    padding: 10px;
    .classify-img {
      width: 74px;
      height: 92px;
      img {
        width: 74px;
        height: 92px;
      }
    }
    .classify-detail {
      width: calc(~"100% - 84.06px");
      margin-left: 10px;
      .classify-title {
